'use client';

import { useState, useEffect } from 'react';
import { useAuth } from '@/lib/contexts/AuthContext';
import Navbar from '@/components/Navbar';
import Link from 'next/link';
import toast from 'react-hot-toast';

interface CartItem {
    product: {
        _id: string;
        name: string;
        price: number;
        images: string[];
        quantity: number;
        slug: string;
    };
    quantity: number;
    price: number;
}

export default function CartPage() {
    const { user, loading: authLoading } = useAuth();
    const [cartItems, setCartItems] = useState<CartItem[]>([]);
    const [total, setTotal] = useState(0);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        if (!authLoading && user) {
            fetchCart();
        } else if (!authLoading && !user) {
            setLoading(false);
        }
    }, [user, authLoading]);

    const fetchCart = async () => {
        try {
            const res = await fetch('/api/cart');
            const data = await res.json();
            if (data.success) {
                setCartItems(data.data.items);
                setTotal(data.data.total);
            }
        } catch (error) {
            toast.error('Failed to load cart');
        } finally {
            setLoading(false);
        }
    };

    const updateQuantity = async (productId: string, newQuantity: number) => {
        try {
            const res = await fetch('/api/cart', {
                method: 'PUT',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ productId, quantity: newQuantity }),
            });
            if (res.ok) {
                fetchCart();
                toast.success('Cart updated');
            }
        } catch (error) {
            toast.error('Update failed');
        }
    };

    const removeItem = async (productId: string) => {
        try {
            const res = await fetch(`/api/cart?productId=${productId}`, { method: 'DELETE' });
            if (res.ok) {
                fetchCart();
                toast.success('Item removed');
            }
        } catch (error) {
            toast.error('Removal failed');
        }
    };

    if (loading) return <div className="min-h-screen bg-gray-50"><Navbar /><div className="p-20 text-center">Loading cart...</div></div>;

    if (!user) {
        return (
            <div className="min-h-screen bg-gray-50">
                <Navbar />
                <div className="max-w-2xl mx-auto px-4 py-20 text-center">
                    <h1 className="text-3xl font-bold mb-4">Your Shopping Cart</h1>
                    <p className="text-gray-600 mb-8 text-lg">Please login to view and manage your cart.</p>
                    <Link href="/login" className="px-8 py-4 bg-purple-600 text-white rounded-2xl font-bold hover:bg-purple-700 transition shadow-lg">Login Now</Link>
                </div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-gray-50">
            <Navbar />
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
                <h1 className="text-4xl font-bold text-gray-900 mb-10">Shopping Cart</h1>

                {cartItems.length === 0 ? (
                    <div className="bg-white rounded-3xl p-16 text-center shadow-sm">
                        <div className="text-6xl mb-6">ðŸ›’</div>
                        <h2 className="text-2xl font-bold text-gray-900 mb-4">Your cart is empty</h2>
                        <p className="text-gray-600 mb-8">Looks like you haven't added anything to your cart yet.</p>
                        <Link href="/shop" className="px-8 py-4 bg-purple-600 text-white rounded-2xl font-bold hover:bg-purple-700 transition shadow-lg">Start Shopping</Link>
                    </div>
                ) : (
                    <div className="grid grid-cols-1 lg:grid-cols-3 gap-12">
                        <div className="lg:col-span-2 space-y-6">
                            {cartItems.map((item) => (
                                <div key={item.product._id} className="bg-white rounded-3xl p-6 shadow-sm flex flex-col sm:flex-row items-center gap-6">
                                    <div className="w-24 h-24 bg-gray-100 rounded-2xl flex-shrink-0 flex items-center justify-center p-2">
                                        {item.product.images?.[0] ? (
                                            <img src={item.product.images[0]} alt={item.product.name} className="max-w-full max-h-full object-contain" />
                                        ) : (
                                            <span className="text-3xl">ðŸ“¦</span>
                                        )}
                                    </div>
                                    <div className="flex-grow text-center sm:text-left">
                                        <h3 className="text-xl font-bold text-gray-900 mb-1">{item.product.name}</h3>
                                        <p className="text-gray-500 text-sm mb-2">${item.price.toFixed(2)} each</p>
                                        <div className="flex flex-wrap items-center justify-center sm:justify-start gap-4">
                                            <div className="flex items-center border border-gray-200 rounded-xl overflow-hidden bg-gray-50">
                                                <button onClick={() => updateQuantity(item.product._id, item.quantity - 1)} className="p-2 px-4 hover:bg-gray-200 transition font-bold">-</button>
                                                <span className="px-4 font-bold min-w-[3rem] text-center">{item.quantity}</span>
                                                <button onClick={() => updateQuantity(item.product._id, item.quantity + 1)} className="p-2 px-4 hover:bg-gray-200 transition font-bold">+</button>
                                            </div>
                                            <button onClick={() => removeItem(item.product._id)} className="text-red-500 hover:text-red-700 text-sm font-bold transition">Remove</button>
                                        </div>
                                    </div>
                                    <div className="text-right flex-shrink-0">
                                        <p className="text-xl font-black text-gray-900">${(item.price * item.quantity).toFixed(2)}</p>
                                    </div>
                                </div>
                            ))}
                        </div>

                        <div className="lg:col-span-1">
                            <div className="bg-white rounded-3xl p-8 shadow-sm border border-purple-50 sticky top-24">
                                <h3 className="text-2xl font-bold text-gray-900 mb-6">Order Summary</h3>
                                <div className="space-y-4 mb-8">
                                    <div className="flex justify-between text-gray-600">
                                        <span>Subtotal</span>
                                        <span className="font-bold">${total.toFixed(2)}</span>
                                    </div>
                                    <div className="flex justify-between text-gray-600">
                                        <span>Tax (10%)</span>
                                        <span className="font-bold">${(total * 0.1).toFixed(2)}</span>
                                    </div>
                                    <div className="flex justify-between text-gray-600">
                                        <span>Shipping</span>
                                        <span className="font-bold">{total > 100 ? 'FREE' : '$10.00'}</span>
                                    </div>
                                    <div className="border-t border-gray-100 pt-4 flex justify-between text-xl font-black text-gray-900">
                                        <span>Total</span>
                                        <span>${(total + total * 0.1 + (total > 100 ? 0 : 10)).toFixed(2)}</span>
                                    </div>
                                </div>
                                <Link href="/checkout" className="block text-center w-full bg-gradient-to-r from-purple-600 to-blue-600 text-white py-5 rounded-2xl font-black text-lg hover:shadow-2xl hover:scale-[1.02] transition active:scale-[0.98]">Proceed to Checkout</Link>
                                <div className="mt-4 text-center">
                                    <Link href="/shop" className="text-purple-600 font-bold hover:underline">Continue Shopping</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}
